'use client'

import { signIn } from 'next-auth/react'
import { useState } from 'react'
import { useSearchParams } from 'next/navigation'
import Link from 'next/link'
import Navbar from '@/components/Navbar'
import Footer from '@/components/Footer'

export default function SignInPage() {
  const searchParams = useSearchParams()
  const callbackUrl = searchParams?.get('callbackUrl') || '/'
  const error = searchParams?.get('error')

  const [email, setEmail] = useState('')
  const [isLoading, setIsLoading] = useState(false)

  const handleEmailSignIn = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    // Try admin login first (no email verification needed)
    const result = await signIn('admin-login', {
      email,
      callbackUrl,
      redirect: false,
    })

    if (result?.error) {
      // If admin login fails, show error
      setIsLoading(false)
      alert('Invalid admin email. Only authorized admin emails can sign in.')
    } else if (result?.ok) {
      // Success! Redirect to callback URL
      window.location.href = callbackUrl
    }
  }

  const handleGoogleSignIn = () => {
    signIn('google', { callbackUrl })
  }

  return (
    <main>
      <Navbar />

      <div className="min-h-screen bg-gray-50 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
        <div className="max-w-md w-full">
          <div className="bg-white rounded-lg shadow-lg p-8">
            <div className="text-center mb-8">
              <h2 className="text-3xl font-bold text-primary mb-2">
                Admin Sign In
              </h2>
              <p className="text-gray-600">
                Enter your authorized admin email to access the admin panel
              </p>
            </div>

            {error && (
              <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg text-red-800">
                {error === 'OAuthSignin' && 'Error signing in with provider'}
                {error === 'OAuthCallback' && 'Error in OAuth callback'}
                {error === 'OAuthCreateAccount' && 'Could not create OAuth account'}
                {error === 'EmailCreateAccount' && 'Could not create email account'}
                {error === 'Callback' && 'Error in callback'}
                {error === 'OAuthAccountNotLinked' && 'Account already exists with different provider'}
                {error === 'EmailSignin' && 'Check your email for the sign in link'}
                {error === 'CredentialsSignin' && 'Invalid credentials'}
                {error === 'SessionRequired' && 'Please sign in to access this page'}
                {!['OAuthSignin', 'OAuthCallback', 'OAuthCreateAccount', 'EmailCreateAccount', 'Callback', 'OAuthAccountNotLinked', 'EmailSignin', 'CredentialsSignin', 'SessionRequired'].includes(error) && 'An error occurred'}
              </div>
            )}

            <div className="space-y-6">
              {/* Email Sign In */}
              <form onSubmit={handleEmailSignIn} className="space-y-4">
                <div>
                  <label htmlFor="email" className="block text-sm font-semibold mb-2">
                    Email Address
                  </label>
                  <input
                    id="email"
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    className="w-full px-4 py-3 border rounded-lg focus:ring-2 focus:ring-primary"
                    placeholder="you@example.com"
                    disabled={isLoading}
                  />
                </div>

                <button
                  type="submit"
                  disabled={isLoading}
                  className="w-full bg-primary text-white py-3 rounded-lg font-bold hover:bg-opacity-90 transition-colors disabled:opacity-50"
                >
                  {isLoading ? 'Signing in...' : 'Sign In'}
                </button>
              </form>

              <div className="mt-6 p-4 bg-blue-50 border border-blue-200 rounded-lg">
                <p className="text-sm text-blue-800">
                  <strong>Note:</strong> Only authorized admin emails can access the admin panel.
                  If you're not an admin, please contact the site administrator.
                </p>
              </div>
            </div>

            <div className="mt-6 text-center text-sm text-gray-600">
              <Link href="/" className="text-secondary hover:underline">
                ‚Üê Back to Home
              </Link>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </main>
  )
}
